import React from "react";
import { NavLink } from "react-router-dom";



const MenuItem = (props) => {

    const { name, iconClassName ,onClick, to, exact} = props;

    return (
    <li>
        <NavLink 
        to = {to} 
        className='menu-item '
        exact
        >
            <div className='menu-icon'>
                <i className={iconClassName}></i>
            </div>
            <span>
                {name}
            </span>
        </NavLink>
    </li>
    )
};

export default MenuItem;