import React, { useState } from 'react';
import "./App.css";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import SideBar from './Components/SideBar';
import SideMenu from './Components/SideMenu';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import HomePage from './Pages/HomePage';
import AboutPage from './Pages/AboutPage';
import TopBar from './Components/TopBar';
import WorkExperiencePage from './Pages/WorkExperiencePage';
import ProjectPage from './Pages/ProjectPage';

const Skills = () => {
  return <h1>Skills</h1>
};



function App() {

  const [inactive, setInactive] = useState(true);

  return (
    <div className='App'>
      <Router>
      <SideMenu
          onCollapse={(inactive) => {
            console.log(inactive);
            setInactive(inactive);
            
          }}
        />
        <TopBar />



        <div className={`container ${inactive ? "" : "inactive"} fixed-size-mobile`}>
          <Routes>
            <Route path = {'/portfolio_website'} element={<HomePage/>}>
            </Route>
            <Route path = {'/portfolio_website/About'} element={<AboutPage/>}>
            </Route>
            <Route path = {'/portfolio_website/Work-Experience'} element={<WorkExperiencePage/>}>
            </Route>
            <Route path = {'/portfolio_website/Projects'} element={<ProjectPage/>}>
            </Route>
            <Route path = {'/portfolio_website/Skills'} element={<Skills/>}>
            </Route>
          </Routes>

        </div>
      </Router>
    </div>
  )
}

export default App