import React, { useState } from 'react';
import { Carousel } from 'react-responsive-carousel';
import Modal from 'react-modal';
import OS_simulator from "../Assets/OS_simulator.png";
import calendarAppAsset from "../Assets/calendarAppAsset.png";
import GeoStacAsset from "../Assets/GeoStacAsset.png";
import mazeasset from "../Assets/maze-make-it-difficult3.jpeg";
import TicTacToeAsset from "../Assets/TicTacToeAsset.png";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader


const ProjectPage = () => {
    const [modalOpen, setModalOpen ] = useState(false);
    const [selectedImageInfo, setSelectedImageInfo] = useState('');

    const images = [
        { img: OS_simulator, info: 'Info about OS Simulator project', title: 'OS Simulator'},
        { img: calendarAppAsset, info: 'Info about Calendar App project', title: 'Andriod App Habit Tracker' },
        { img: GeoStacAsset, info: 'Info about GeoStac project', title: 'Capstone Project: GeoStac' },
        { img: mazeasset, info: 'Info about Maze project', title: "Maze Solver AI Project"},
        { img: TicTacToeAsset, info: 'Info about Tic Tac Toe project', title : "AI Tic Tac Toe Game" }
    ];

    const [currentTitle, setCurrentTitle] = useState(images[0].title);

    const openModalWithInfo = (info) => {
        setSelectedImageInfo(info);
        setModalOpen(true);
    };

    const handleSelect = (index) => {
        setCurrentTitle(images[index].title);
    };

    return (
        <div className='page'>
            <h1 className='about-page-header'>
                Project Showcase:
            </h1>
            <h2 className='carousel-title'>{currentTitle}</h2>
            <div className="carousel-container">
                <Carousel
                    showThumbs={false}
                    showStatus={false}
                    showIndicators={true}
                    infiniteLoop={true}
                    useKeyboardArrows={true}
                    autoPlay={false}
                    onChange={handleSelect}
                >
                    {images.map((image, index) =>
                        <div key={index} onClick={() => openModalWithInfo(image.info)}>
                            <img src={image.img} alt={`Slide ${index}` } className="carousel-image" />
                            <p className="overlay-text">Click for more info</p>
                        </div>
                    )}
                </Carousel>
            </div>

            <Modal
                isOpen={modalOpen}
                onRequestClose={() => setModalOpen(false)}
            >
                <h2>Project Information</h2>
                <p>
                    {selectedImageInfo}
                </p>
                <button onClick={() => setModalOpen(false)}>Close</button>
            </Modal>
        </div>
    );
};

export default ProjectPage;
