import React from 'react';
import { ReactComponent as WorkIcon } from "../Assets/work.svg";
import {ReactComponent as SchoolIcon } from "../Assets/school.svg";

import timelineElements from "../Components/timelineElements";

import {
    VerticalTimeline,
    VerticalTimelineElement} from "react-vertical-timeline-component";

import "react-vertical-timeline-component/style.min.css";

const WorkExperiencePage = () =>{
    let workIconStyles = { background: "#06D6A0" };
    let schoolIconStyles = { background: "#f9c74f" };

    return (
        <div className='workexperience-page'>
            <VerticalTimeline>
                {
                    timelineElements.map((element) => {
                        let isWorkIcon = element.icon === "work";
                        let showButton = element.buttonText !== undefined &&
                        element.buttonText !==null && element.buttonText !== "";

                        return(
                        <VerticalTimelineElement
                         key={element.key}
                         date={element.date}
                         dateClassName = "date"
                         iconStyle = {isWorkIcon ? workIconStyles : schoolIconStyles}
                         icon={isWorkIcon ? <WorkIcon /> : <SchoolIcon />}
                        >
                            <h3 className='verticle-timeline-element-title'>
                                {element.title}
                            </h3>
                            <h5 className="verticle-timeline-element-subtitle">
                                {element.location}
                            </h5>
                            <p id="description">
                                {element.description}
                            </p>
                            {showButton && (<a  className={`button ${isWorkIcon ? "workButton" :
                            "schoolButton"}`}href="/">{element.buttonText}</a>)}
                        </VerticalTimelineElement>
                        );
                    })
                    
                }
            </VerticalTimeline>
        </div>
    );
};

export default WorkExperiencePage;